@import '../../App';

.containerCarts {
  display: grid;
  grid-template-columns: 73% 25%;
  justify-content: space-between;
  margin: 2rem auto;
  width: 97vw;
  .carts {
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    width: 100%;
    .cart {
      display: grid;
      grid-template-columns: 25% 72%;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 1rem;
      background: $mainBackground;
      padding: 1.8rem 1.6rem;
      border-radius: 2rem;
      .product {
        img {
          width: 100%; } }
      .cartDescription {
        display: flex;
        justify-content: space-between;
        flex-direction: column;
        .descriptionContainer {
          display: flex;
          justify-content: space-between;
          flex-direction: column;
          h2 {
            text-align: center;
            padding-bottom: 0.5rem; }
          p {
            text-align: left;
            text-indent: 1rem;
            padding-bottom: 0.5rem; } }
        .priceColorContainer {
          display: flex;
          justify-content: space-between;
          padding-top: 20px;
          padding-bottom: 20px;
          .price {
            width: 30%;
            align-self: center;
            text-align: center;
            font-size: 1.2rem; }
          .colorContainer {
            width: 20rem;
            padding-top: 0.5rem;
            padding-bottom: 0.5rem;
            display: flex;
            justify-content: space-between;
            align-items: center;
            position: relative;
            background: $mainWhite;
            border-radius: 1rem;
            transition: $mainTransition;
            .colorArrowContainer {
              h5 {
                text-align: center;
                padding-left: 0.6rem; }
              .color {
                position: absolute;
                z-index: 1;
                left: 0;
                right: 0;
                width: 100%;
                margin: 0 auto;
                height: 0;
                overflow: hidden;
                transition: $mainTransition;
                li {
                  height: 0;
                  margin: 0;
                  overflow: hidden;
                  background: $mainWhite;
                  position: relative; }
                //transition: $mainTransition
                li:last-child {
                  border-bottom-left-radius: 1rem;
                  border-bottom-right-radius: 1rem; }
                li div {
                  position: absolute;
                  z-index: 2;
                  left: 0;
                  width: 95%;
                  margin-left: 0;
                  padding-top: 0.1rem;
                  padding-bottom: 0.15rem;
                  padding-left: 0.5rem;
                  border-bottom-right-radius: 0.5rem;
                  border-top-right-radius: 0.5rem; }
                li div:hover {
                  background: $mainBackground;
                  transition: $mainTransition; } } }
            .arrowContainer {
              padding-right: 1rem;
              //margin-bottom: 0.4rem
              align-self: center;
              .arrowDown {
                border: solid black;
                border-width: 0 3px 3px 0;
                display: inline-block;
                padding: 2px;
                transform: rotate(45deg); } }
            &:hover {
              border-bottom-right-radius: unset;
              border-bottom-left-radius: unset; }
            &:hover .arrowDown {
              border-width: 3px 0 0 3px; }
            &:hover .color {
              height: 100%;
              overflow: visible;
              transition: $mainTransition; }
            &:hover .color li {
              height: 100%;
              overflow: visible;
              font-size: 0.7rem;
              padding: 0.5rem;
              margin: 0; } } }
        .capacityContainer {
          display: flex;
          .checkboxContainer {
            display: flex;
            justify-content: left;
            align-items: center;
            margin-bottom: 13px;
            margin-right: 15px;
            label {
              position: relative;
              span {
                margin-left: 0.5rem; } }
            input[type='checkbox'] {
              display: none; }
            label:before {
              content: "";
              display: inline-block;
              width: 23px;
              height: 23px;
              border: 1px solid $mainGrey;
              background: $mainGrey;
              border-radius: 5px;
              vertical-align: middle; }
            input[type='checkbox']:checked + label span::before {
              content: "";
              width: 17px;
              height: 17px;
              background: $mainGold;
              border-radius: 5px;
              box-shadow: $shadow;
              position: absolute;
              left: 3.5px;
              top: 3.5px; } } }
        .countContainer {
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-top: 30px;
          .countProduct {
            display: flex;
            justify-content: space-between;
            width: 92px;
            padding: 0.4rem 0.8rem;
            background: $mainGrey;
            border-radius: 1rem;
            font-size: 0.9rem;
            div {
              cursor: pointer; } }
          .btnBuy {
            width: 302px;
            text-align: center;
            padding: 0.4rem 0.8rem;
            border-radius: 1rem;
            color: $mainWhite;
            text-transform: uppercase;
            font-size: 0.9rem;
            background: $mainGradient; } } } } }
  .totalCart {
    width: 100%;
    height: 200px;
    background: $mainBackground;
    border-radius: 1rem;
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 1rem 0.8rem;
    margin: 0 auto;
    div {
      text-align: center;
      padding: 0.8rem;
      font-size: 1.2rem; }
    .buyBtn {
      background: $mainGradient;
      padding: 0.3rem;
      border-radius: 2rem;
      color: $mainWhite; } } }


